<template>
  <div class="roy-social"
       :class="{'absolute': absolute, 'fixed': fixed}"
       :style="{'top': defaultTop, 'bottom': defaultBottom, 'left': defaultLeft, 'right': defaultRight}"
  >
    <v-btn flat fab v-for="(item, index) in items" :key="index" :href="item.url" target="_blank">
      <v-icon :style="{'color': item.color}">{{item.icon}}</v-icon>
    </v-btn>
  </div>
</template>

<script>
  export default {
    data () {
      return {
        items: [
          {name: 'Facebook', icon: 'fab fa-facebook-f', color: '#3b5998', url: 'https://www.facebook.com'},
          {name: 'Instagram', icon: 'fab fa-instagram', color: '#5e1d1b', url: 'https://www.twitter.com'},
          {name: 'Twitter', icon: 'fab fa-twitter', color: '#00aced', url: 'https://www.instagram.com'}
        ]
      }
    },
    props: {
      absolute: {
        type: Boolean,
        default: false
      },
      fixed: {
        type: Boolean,
        default: false
      },
      top: [Boolean, String],
      bottom: [Boolean, String],
      left: [Boolean, String],
      right: [Boolean, String]
    },
    computed: {
      defaultTop () {
        let val = 'unset'
        if (this.top.length) val = this.top
        else if (typeof this.top === 'boolean' && this.top) return '16px'
        return val
      },
      defaultBottom () {
        let val = 'unset'
        if (this.bottom.length) val = this.bottom
        else if (typeof this.bottom === 'boolean' && this.bottom) return '16px'
        return val
      },
      defaultLeft () {
        let val = 'unset'
        if (this.left.length) val = this.left
        else if (typeof this.left === 'boolean' && this.left) return '16px'
        return val
      },
      defaultRight () {
        let val = 'unset'
        if (this.right.length) val = this.right
        else if (typeof this.right === 'boolean' && this.right) return '16px'
        return val
      }
    }
  }
</script>

<style scoped lang="stylus">
  .roy-social
    display inline-table
  .absolute
    z-index 4
    position absolute
  .fixed
    z-index 4
    position fixed
</style>


