import Vue from 'vue'
import Router from 'vue-router'
import HomePage from '@/components/pages/Home'
import AboutPage from '@/components/pages/AboutUs'
import ProductionPage from '@/components/pages/Production'
import ContactPage from '@/components/pages/Contact'
import CategoryPage from '@/components/pages/Category'
import ItemsPage from '@/components/pages/Items'
import ItemPage from '@/components/pages/Item'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    { path: '*', component: HomePage },
    {
      path: '/',
      name: 'Home',
      component: HomePage
    },
    {
      path: '/about',
      name: 'About',
      component: AboutPage
    },
    {
      path: '/production',
      name: 'Production',
      component: ProductionPage
    },
    {
      path: '/contact',
      name: 'Contact',
      component: ContactPage
    },
    {
      path: '/item/:id',
      name: 'Item',
      component: ItemPage
    },
    {
      path: '/category/:cat_id/:sub_cat_id?',
      name: 'Category',
      component: CategoryPage,
      children: [
        {
          path: 'products',
          component: ItemsPage
        }
      ]
    }
  ]
})



