import Vue from 'vue'
import Vuex from 'vuex'
import VuejsStorage from 'vuejs-storage'

Vue.use(Vuex)
Vue.use(VuejsStorage)

const TIMEOUT = 15000

export const store = new Vuex.Store({
  state: {
    user: {
      cart: []
    },
    selectedCategory: {},
    categories: [
      {
        id: 1,
        name: 'Air Brake Compressors',
        image: 'http://www.robur-bremse.de/rsm/grup-compressor.jpg',
        sub_cats: [
          {id: 5, name: 'DAF', image: 'http://www.robur-bremse.de/UserFiles/catlogo/226.jpg', products: []},
          {id: 6, name: 'IVECO-DEUTZ', image: 'http://www.robur-bremse.de/UserFiles/catlogo/227.jpg'},
          {id: 7, name: 'MAN', image: 'http://www.robur-bremse.de/UserFiles/catlogo/228.jpg'},
          {id: 8, name: 'MERCEDES', image: 'http://www.robur-bremse.de/UserFiles/catlogo/229.jpg'},
          {id: 9, name: 'AMERICAN TYPES', image: 'http://www.robur-bremse.de/UserFiles/catlogo/230.jpg'},
          {id: 10, name: 'RENAULT', image: 'http://www.robur-bremse.de/UserFiles/catlogo/231.jpg'},
          {id: 11, name: 'SCANIA', image: 'http://www.robur-bremse.de/UserFiles/catlogo/232.jpg'},
          {id: 12, name: 'VOLVO', image: 'http://www.robur-bremse.de/UserFiles/catlogo/233.jpg'},
          {id: 13, name: 'OTHERS', image: 'http://www.robur-bremse.de/UserFiles/catlogo/234.jpg'}
        ]
      },
      {id: 2, name: 'Clutch Servos', image: 'http://www.robur-bremse.de/rsm/grup-clutch-servo.jpg', sub_cats: []},
      {id: 3, name: 'Master Cylinders', image: 'http://www.robur-bremse.de/rsm/grup-master-cylinder.jpg', sub_cats: []},
      {id: 4, name: 'Gearbox & Safety Valves', image: 'http://www.robur-bremse.de/rsm/grup-gearbox.jpg', sub_cats: []}
    ]
  },
  mutations: {
    addToCart (state, item) {
      state.user.cart.push(item)
    },
    selectCategory (state, category) {
      state.selectedCategory = category
    }
  },
  getters: {
    getCAtegoryProducts (state) {
      return state.products.filter(category => category.id)
    }
  },
  actions: {
    getCategories (context, request) {
      return new Promise((resolve, reject) => {
        Vue.http.post('/categories', request, {timeout: TIMEOUT})
          .then(response => resolve(response))
          .catch(errors => reject(errors))
      })
    },
    getProducts (context, request) {
      return new Promise((resolve, reject) => {
        Vue.http.post('/categories', request, {timeout: TIMEOUT})
          .then(response => resolve(response))
          .catch(errors => reject(errors))
      })
    }
  }
})



