<template>
  <v-app>
    <v-content>
      <roy-social absolute top="100px" right></roy-social>

      <v-container fluid class="pa-0">
        <v-btn
                class="hidden-xs-only"
                color="black"
                dark
                fixed
                top
                right
                fab
                @click.native="showSearch = !showSearch"
        >
          <v-icon v-if="!showSearch">fa-search</v-icon>
          <v-icon v-else>fa-times</v-icon>
        </v-btn>

        <div class="search-box pa-3">
          <v-fade-transition>
            <v-text-field
                    prepend-icon="fa-search"
                    v-show="showSearch"
                    label="Buscar por: Ref num, Oem num, Roy num"
            ></v-text-field>
          </v-fade-transition>
        </div>


        <v-layout column align-center>
          <img src="@/assets/images/logo.png" alt="Logo" class="mb-0" height="150px">
        </v-layout>
        <!--Menu-->
        <v-layout>
          <v-toolbar dark>
            <v-spacer></v-spacer>
            <v-toolbar-items>
              <template v-for="(tab, index) in tabs">
                <v-menu :nudge-bottom="55" open-on-hover v-if="tab.subTabs && tab.subTabs.length > 0">
                  <v-btn
                          v-esle
                          slot="activator"
                          :key="index"
                          flat
                  >
                    {{tab.text}}
                  </v-btn>
                  <v-list>
                    <v-list-tile v-for="(item, i) in tab.subTabs" :key="i" :to="{name: item.route}">
                      <v-list-tile-title>{{item.text}}</v-list-tile-title>
                    </v-list-tile>
                  </v-list>
                </v-menu>
                <v-btn
                        v-if="!tab.subTabs"
                        :key="index"
                        flat
                        :to="{name: tab.route}"
                >
                  {{tab.text}}
                </v-btn>
              </template>
            </v-toolbar-items>
            <v-spacer></v-spacer>
            <v-badge left>
              <span slot="badge" v-text="cart.length"></span>
              <v-icon>fa-shopping-cart</v-icon>
            </v-badge>
          </v-toolbar>
        </v-layout>

        <router-view/>
      </v-container>

    </v-content>
    <v-footer :fixed="true" app dark class="pa-4">
      <v-spacer></v-spacer>
      <span>RPC air brake parts &copy; 2017</span>
    </v-footer>
  </v-app>
</template>

<script>
  import {VBadge, VSpeedDial, VTextField, VMenu} from 'vuetify'
  import RoySocial from './components/partial/RoySocial'

  export default {
    data () {
      return {
        showSearch: false,
        tabs: [
          {text: 'Inicio', route: 'Home'},
          {text: 'Acerca de', route: 'About', subTabs: [
            {text:'La Compañia', route: 'About'},
            {text:'Mision y Vision', route: 'MV'}
          ]},
          {text: 'Instructivos', route: 'Contact', subTabs: [
            {text:'Instalacion de repuestos', route: 'MV'},
            {text:'Instrucciones del servicio', route: 'MV'}
          ]},
          {text: 'Contactanos', route: 'Contact'}
        ]
      }
    },
    computed: {
      cart () {
        return this.$store.state.user.cart
      }
    },
    name: 'App',
    components: {
      VSpeedDial,
      VBadge,
      VMenu,
      VTextField,
      RoySocial
    }
  }
</script>



