<template>
  <v-slide-y-transition >
    <section class="home-page">
      <!--Carousel-->
      <v-layout>
        <v-carousel dark>
          <v-carousel-item v-for="(item,i) in carousel" :src="item" :key="i"></v-carousel-item>
        </v-carousel>
      </v-layout>
      <!--Categories-->
      <v-layout wrap row>
        <v-flex
            v-for="(category, index) in categories"
            :key="index"
            md3
            class="text-xs-center"
            @click="selectCategory(category)"
        >
          <v-card flat :to="{name: 'Category', params: {cat_id: category.id}}">
            <v-card-media
                    :src="category.image"
                    width="216px"
                    height="130px"
            >
              <v-container fill-height fluid>
                <v-layout fill-height>
                  <v-flex xs12 align-end flexbox>
                    <span class="headline white--text" v-text="category.name"></span>
                  </v-flex>
                </v-layout>
              </v-container>
            </v-card-media>
          </v-card>
        </v-flex>
      </v-layout>

      <v-layout wrap row>
        <v-flex xs12 md6>
          <v-card height="170px">
            <v-container fluid grid-list-lg>
              <v-layout row>
                <v-flex xs7>
                  <div>
                    <div class="headline">Acerca de nostros</div>
                    <div>Lorem ipsum dolor sit amet, consectetur adipisicing elit. Doloremque eos incidunt laudantium nemo odio quidem quo, sit tenetur veritatis voluptatem. Ex fugiat laudantium non perferendis, praesentium reprehenderit repudiandae unde veritatis?</div>
                  </div>
                </v-flex>
                <v-flex xs5>
                  <v-card-media
                          src="http://www.robur-bremse.de/rsm/girisResim1.png"
                          height="125px"
                          contain
                  ></v-card-media>
                </v-flex>
              </v-layout>
            </v-container>
          </v-card>
        </v-flex>
        <v-flex xs12 md6>
          <v-card height="170px">
            <v-container fluid grid-list-lg>
              <v-layout row>
                <v-flex xs7>
                  <div>
                    <div class="headline">Produccion</div>
                    <div>Lorem ipsum dolor sit amet, consectetur adipisicing elit. Alias, animi dolore doloremque enim illum, itaque iusto labore nam obcaecati officiis omnis quae quos ratione sapiente, sit sunt voluptatibus? Est, similique!</div>
                  </div>
                </v-flex>
                <v-flex xs5>
                  <v-card-media
                          src="http://www.robur-bremse.de/rsm/girisResim2.png"
                          height="125px"
                          contain
                  ></v-card-media>
                </v-flex>
              </v-layout>
            </v-container>
          </v-card>
        </v-flex>
      </v-layout>
    </section>
  </v-slide-y-transition>
</template>

<script>
  import { VCarousel, VCarouselItem } from 'vuetify/es5/components/VCarousel'
  import { VCard, VCardMedia } from 'vuetify/es5/components/VCard'

  export default {
    data () {
      return {
        carousel: [
          'http://www.robur-bremse.de/UserFiles/galeri/201505121139361431419976.png',
          'http://www.robur-bremse.de/UserFiles/galeri/201505121139531431419993.png',
          'http://www.robur-bremse.de/UserFiles/galeri/201507100744051436503445.png',
          'http://www.robur-bremse.de/UserFiles/galeri/201505121234281431423268.png',
          'http://www.robur-bremse.de/UserFiles/galeri/201507100744151436503455.png'
        ]
      }
    },
    methods: {
      selectCategory (category) {
        this.$store.commit('selectCategory', category)
      }
    },
    computed: {
      categories () {
        return this.$store.state.categories
      }
    },
    components: {
      VCarousel,
      VCarouselItem,
      VCard,
      VCardMedia
    }
  }
</script>



