<template>
  <v-slide-y-transition mode="out-in">
    <section class="category-page pa-3">

      <v-layout>
        <v-breadcrumbs large>
          <v-icon slot="divider">fa-caret-right</v-icon>
          <v-breadcrumbs-item :to="{name: 'Home'}">INICIO</v-breadcrumbs-item>
          <v-breadcrumbs-item :to="{name: 'Home'}" disabled>{{selectedCategory.name}}</v-breadcrumbs-item>
        </v-breadcrumbs>
      </v-layout>



      <v-container grid-list-md fluid>
        <v-layout wrap row>
          <v-flex xs12 sm6 md4 v-if="selectedCategory.sub_cats.length > 0" v-for="(sub_cat, index) in selectedCategory.sub_cats" :key="sub_cat.id">
            <v-card flat tile>
              <v-card-media
                      :src="sub_cat.image"
                      height="150px"
              >
                <v-container fill-height fluid>
                  <v-layout fill-height>
                    <v-flex xs12 align-end flexbox>
                      <span class="headline white--text">{{sub_cat.name}}</span>
                    </v-flex>
                  </v-layout>
                </v-container>
              </v-card-media>
            </v-card>
          </v-flex>
          <v-flex xs12 v-else>
            <h3>No hay productos en esta categoria</h3>
          </v-flex>
        </v-layout>
      </v-container>
    </section>
  </v-slide-y-transition>
</template>

<script>
  import {VCard, VCardMedia} from 'vuetify/es5/components/VCard'
  import {VBreadcrumbs, VBreadcrumbsItem} from 'vuetify/es5/components/VBreadcrumbs'

  export default {
    data () {
      return {}
    },
    created () {
      if (this.selectCategory.items) this.$route.push({name: 'Items'})
    },
    computed: {
      selectedCategory () {
        return this.$store.state.selectedCategory
      }
    },
    components: {
      VCard,
      VCardMedia,
      VBreadcrumbs,
      VBreadcrumbsItem
    }
  }
</script>



