<template>
  <v-slide-y-transition mode="out-in">
    <section class="contant-page pa-3">
      <v-layout>
        <v-flex class="text-xs-center">
          <h1>Contactanos</h1>
        </v-flex>
      </v-layout>

      <v-layout row wrap>
        <v-flex md7 class="pa-3">
          <h3>Dirección</h3>
          <address>
            <strong>COMPRESORES ROY, ARAGUA C.A.</strong><br>
            <strong>R.I.F.:</strong> J-07587206-1<br>
            <strong>Teléfono:</strong> +58.243.269.4561<br>
            <strong>Fax:</strong> +58.243.269.3930.<br>
            <strong>Correo:</strong> comroyarca.ventas@gmail.com<br>
            <strong>Dirección en Venezuela:</strong> Av. Intercomunal Turmero-Maracay. Sector: La Herrereña. Edificio: ROY. (202-2). Maracay. Edo. Aragua. Venezuela (frente a los Bancos Mercantil y Banesco).<br>
          </address>
          <br>
          <address>
            <strong>ROY PANAMÁ CORP.</strong><br>
            <strong>R.U.C.:</strong> 155604765-2-2015 DV70<br>
            <strong>Correo:</strong> roypanamacorp@gmail.com<br>
            <strong>Dirección en Panamá:</strong> Obarrio, Av. Samuel Lewis, Calle 57, Edificio Addison House, Primer Piso, Oficina No. 19. Ciudad de Panamá, Panamá.
          </address>
        </v-flex>
        <v-flex md5 class="pa-3">
          <v-form valid="valid">
            <h3>Formulario de contacto</h3>
            <v-text-field v-model="request.full_name" label="Nombre completo" placeholder="Ej: John Doe"></v-text-field>
            <v-text-field v-model="request.email" label="Correo electronico" placeholder="Ej: john.doe@gmail.com"></v-text-field>
            <v-text-field v-model="request.phone" label="Telefono" placeholder="Ej: +58 4144442233"></v-text-field>
            <v-text-field
                    v-model="request.message"
                    label="Mensaje"
                    multi-line
            ></v-text-field>

            <v-btn type="submit" color="primary">Enviar</v-btn>
          </v-form>
        </v-flex>
        <v-flex xs12 class="text-xs-center pa-4">
          <h3>Mapa</h3>
          <gmap-map
                  style="width: 100%; height: 400px"
                  :center="{lat: 10, lng: 49}"
                  :zoom="16"
                  map-type-id="terrain"
          >
            <gmap-marker
                    ref="marker"
                    :position="request.location"
            ></gmap-marker>
          </gmap-map>
        </v-flex>
      </v-layout>
    </section>
  </v-slide-y-transition>
</template>

<script>
  import Vue from 'vue'
  import {VForm, VTextField} from 'vuetify'

  import * as VueGoogleMaps from 'vue2-google-maps'

  Vue.use(VueGoogleMaps, {
    load: {
      key: 'AIzaSyCzUNka_7Jkt5BQS45jnHlwEUbYOWBQK8Q',
      libraries: 'places'
    }
  })

  export default {
    data () {
      return {
        valid: false,
        request: {
          full_name: null,
          email: null,
          phone: null,
          message: null
        }
      }
    },
    components: {
      VForm,
      VTextField
    }
  }
</script>


